.App {
  text-align: center;
}

.start-btn {
  background: transparent;
  border: none;
}

.App-logo {
  height: 40vmin;
  cursor: pointer;
}

.App-logo:hover {
  transform: scale(1.05);
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.oscillate {
  animation: oscillate 2s infinite;
}

@keyframes oscillate {
  0% { transform: translateX(0); fill: #61DAFB; }

  40% { fill: #61DAFB; }
  50% { transform: translateX(600px); fill: #FB61DA; }

  90% { fill: #FB61DA; }
  100% { transform: translateX(0); fill: #61DAFB }
}